<template>
  <div>
    <socialMedia > </socialMedia>
    <div id="contacto" class="p-5 mt-5 section section-contact-us text-center">
      <div class="container">
        <h2 class="title">Quiere hacer parte de esto?</h2>
        <p class="description">Escribanos sus dudas, y con gusto las resolveremos.</p>
        <div class="row">
          <div class="col-lg-6 text-center ml-auto mr-auto col-md-8">
            <fg-input
              class="input-lg"
              placeholder="Nombre..."
              v-model="form.firstName"
              addon-left-icon="now-ui-icons users_circle-08"
            >
            </fg-input>
            <fg-input
              class="input-lg"
              placeholder="Telfono..."
              v-model="form.email"
              addon-left-icon="now-ui-icons tech_mobile"
            ></fg-input>
            <fg-input
              class="input-lg"
              placeholder="Ciudad..."
              v-model="form.email"
              addon-left-icon="now-ui-icons location_world"
            ></fg-input>
            <fg-input
              class="input-lg"
              placeholder="Pas..."
              v-model="form.email"
              addon-left-icon="now-ui-icons location_world"
            >
            </fg-input>
            <div class="textarea-container">
              <textarea
                class="form-control"
                name="name"
                rows="4"
                cols="80"
                v-model="form.message"
                placeholder="Escribanos un mensaje..."
              ></textarea>
            </div>
            <div class="send-button">
              <n-button type="primary" round block size="lg"
                >Enviar</n-button
              >
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { Button, FormGroupInput, Modal } from '@/components';
import socialMedia from './../layout/SocialMedia';


export default {
  name: 'landing',
  bodyClass: 'landing-page',
  components: {
    [Button.name]: Button,
    [FormGroupInput.name]: FormGroupInput, 
    socialMedia
  },
  data() {
    return {
      page: 0,
      form: {
        firstName: '',
        email: '',
        message: ''
      },
      publicPath: process.env.BASE_URL != '/' ? process.env.BASE_URL : "img/",
        options: {
          responsive: [
            {end: 576, size: 1}, 
            {start: 576, end: 768, size: 2},
            {start: 768, end: 992, size: 3},
            {size: 3}
          ],
          navigation: {
            // when to show navigation
            start: 10,
            color: '#000'
          },
          list: {
            // 1200 because @media (min-width: 1200px) and therefore I want to switch to windowed mode
            windowed: 1200,
            
            // Because: #app {padding: 80px 24px;}
            padding: 24
          }
        },
    };
  },methods: {
    Cambiarfondo(dataset){
      this.page = dataset
    },
    // ir(link){
    //   $router.push({ path: parameter })
    // }
  }, 
  mounted(){
    const map = document.querySelector('#contacto')
    addEventListener('DOMContentLoaded', event => {
      if (window.location.hash === 'contacto') {
        map.scrollIntoView({behavior: "smooth", block: "center"});
      }
    });
  }
};
</script>

<style>


nav.navbar  div.container {
    margin-left:0% !important;
}


</style>